export default {
	name: 'temHome',
	props: {
		msg: {
			type: String,
			default: ''
		},
		type: {
			type: String,
			default: ''
		},
		fromm: {
			type: String,
			default: ''
		}
	},
	data() {
		return {
			qrCode: '',
			qrCodeType: '',
			xgData: [],
			current: -1,
			chooseId: '',
			choosePlace: '',
			imgrow: [],
			lon: '',
			lat: '',
			commitTime: 0,
			uploadImgRow: [],
			laizi: ''
		};
	},
	methods: {
		//图片相关
		ChooseImage(index) {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['camera'], //从相册选择
				success: res => {
					console.log(res);
					this.imgrow[index].url.push(res.tempFilePaths[0]);
				}
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgrow[e.currentTarget.dataset.index].url,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			console.log(e);
			uni.showModal({
				title: '删除',
				content: '确定要删除这张图片吗？',
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						this.imgrow[e.currentTarget.dataset.chooes].url.splice(e.currentTarget.dataset.index, 1);
					}
				}
			});
		},
		showModel(title) {
			uni.showModal({
				title: '提示',
				content: title,
				showCancel: false,
				success: function(res) {
					if (res.confirm) {
					}
				}
			});
		},
		SwitchA(e) {
			for (let name of this.xgData) {
				if (name.Name == e.currentTarget.dataset.title) {
					for (let word of name.Detail) {
						if (word.content == e.currentTarget.dataset.info.content) {
							word.isCheck = e.detail.value;
						}
					}
				}
			}
			// console.log(this.xgData)
		},
		getData() {
			var self = this;
			uni
				.request({
					url: self.$store.state.urlPrefix + '/School/Get_HD',
					data: {
						QRCode: self.qrCode,
						type: self.qrCodeType
					}
				})
				.then(data => {
					var [error, res] = data;

					if (res.data.code == 0) {
						self.xgData = res.data.data;
						self.choosePlace = self.xgData[0].PlaceNo;
						for (let cont of self.xgData) {
							self.imgrow.push({
								name: cont.ID,
								url: []
							});
							if (cont.Detail[cont.Detail.length - 1] == ';') {
								cont.Detail = cont.Detail.substr(0, cont.Detail.length - 1);
							}
							let content = cont.Detail.split(';');
							cont.Detail = [];
							for (let word of content) {
								cont.Detail.push({
									isCheck: false,
									content: word
								});
							}
						}

						console.log(self.imgrow);
					} else {
						uni.showToast({
							title: '扫描失败',
							duration: 2000,
							icon: 'loading'
						});
					}
				});
		},
		nextStep() {
			let judge = [];
			let DangerIndex = [];
			let pcyd = [];

			//获取排查要点
			for (let levelOne of this.xgData) {
				for (let levelTwo of levelOne.Detail) {
					judge.push(levelTwo.isCheck);
					if (levelTwo.isCheck == false) {
						pcyd.push({
							content: levelTwo.content,
							id: levelOne.ID,
							position: ''
						});
					}
				}
			}
			//合并对象
			let result = {};
			pcyd.forEach(item => {
				if (result[item.id]) {
					result[item.id] += item.content;
				} else {
					result[item.id] = item.content;
				}
			});

			let postData = [];
			for (let k in result) {
				postData.push({ HDID: k, CZWT: result[k], position: '' });
			}

			//判断是否安全
			if (
				judge.every(item => {
					return item == true;
				})
			) {
				if (this.fromm =='隐患上报') {
					this.HYsafeCommit();
				} else {
					this.safeCommit();
				}
			} else {
				for (let i = 0; i < this.xgData.length; i++) {
					if (
						!this.xgData[i].Detail.every(item => {
							return item.isCheck == true;
						}) &&
						this.imgrow[i].url.length == 0
					) {
						this.showModel('请完善隐患照片！');
						break;
					} else if (
						!this.xgData[i].Detail.every(item => {
							return item.isCheck == true;
						})
					) {
						DangerIndex.push(i);
					}
				}
			}
			var len = 0;
			for (let j = 0; j < DangerIndex.length; j++) {
				let imgNumber = this.imgrow[DangerIndex[j]].url.length;
				for (let zzy of this.imgrow[DangerIndex[j]].url) {
					this.uploadImgRow.push({
						id: this.imgrow[DangerIndex[j]].name,
						url: zzy
					});
				}
				len = len + imgNumber;
			}

			if (len > 0) {
				this.uploadSafeImg(len, postData);
			}
		},
		getLocation() {
			var self = this;
			uni.getLocation({
				type: 'gcj02',
				success: function(res) {
					self.lon = res.longitude;
					self.lat = res.latitude;
				}
			});
		},
		uploadSafeImg(len, postData) {
			var self = this;
			var postImgUrl = self.$store.state.urlPrefix + '/Process/Everyone_safe_images2';
			uni.uploadFile({
				url: postImgUrl,
				filePath: self.uploadImgRow[self.commitTime].url,
				name: 'image',
				formData: {
					RYXH: self.$store.state.userInfo.RYXH,
					HDID: self.uploadImgRow[self.commitTime].id
				},
				success(res) {
					self.commitTime = self.commitTime + 1;
					if (res.data == 'ok') {
						if (self.commitTime < len) {
							self.uploadSafeImg(len, postData);
						} else {
							//commit words
							// self.dangerCommit(postData);
							if (self.fromm == '隐患上报') {
								self.HYdangerCommit()
							} else {
								self.dangerCommit(postData);
							}
							
							self.commitTime = 0;
						}
					} else {
						uni.hideLoading();
						uni.showModal({
							title: '提示',
							content: '上报失败！',
							showCancel: false,
							success: function(res) {
								if (res.confirm) {
									uni.reLaunch({
										url: '/pages/basic/home/home'
									});
								}
							}
						});
					}
				}
			});
		},
		safeCommit() {
			uni.showLoading({
				title: '提交中。。。',
				mask: true
			});
			var self = this;
			uni
				.request({
					url: self.$store.state.urlPrefix + '/School/ScanQRCode_Check_Safe',
					data: {
						type: self.qrCodeType,
						RYXH: self.$store.state.userInfo.RYXH,
						PlaceNo: self.choosePlace
					}
				})
				.then(data => {
					var [error, res] = data;
					uni.hideLoading();
					if (res.data.code == 0) {
						uni.showModal({
							title: '提示',
							content: '提交成功！',
							showCancel: false,
							confirmText: '确定',
							success: res => {
								if (res.confirm) {
									uni.reLaunch({
										url: '/pages/basic/home/home'
									});
								}
							}
						});
					} else if (res.data.code == 1) {
						uni.showModal({
							title: '提示',
							content: res.data.msg,
							showCancel: false,
							confirmText: '确定',
							success: res => {
								if (res.confirm) {
									uni.reLaunch({
										url: '/pages/basic/home/home'
									});
								}
							}
						});
					} else {
						uni.showModal({
							title: '提示',
							content: '上传失败',
							showCancel: false,
							confirmText: '确定',
							success: res => {
								if (res.confirm) {
									uni.reLaunch({
										url: '/pages/basic/home/home'
									});
								}
							}
						});
					}
				});
		},

		dangerCommit(postData) {
			var self = this;
			let ddd = JSON.stringify(postData);
			uni
				.request({
					url: self.$store.state.urlPrefix + '/School/ScanQRCode_Check_Danger',
					method: 'POST',
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					data: {
						type: self.qrCodeType,
						RYXH: self.$store.state.userInfo.RYXH,
						PlaceNo: self.choosePlace,
						hdDetail: ddd,
						lat: self.lat,
						lon: self.lon
					}
				})
				.then(data => {
					var [error, res] = data;
					uni.hideLoading();
					if (res.data.code == 0) {
						uni.showModal({
							title: '提示',
							content: '提交成功！',
							showCancel: false,
							confirmText: '确定',
							success: res => {
								if (res.confirm) {
									uni.reLaunch({
										url: '/pages/basic/home/home'
									});
								}
							}
						});
					} else if (res.data.code == 1) {
						uni.showModal({
							title: '提示',
							content: res.data.msg,
							showCancel: false,
							confirmText: '确定',
							success: res => {
								if (res.confirm) {
									uni.reLaunch({
										url: '/pages/basic/home/home'
									});
								}
							}
						});
					} else {
						uni.showModal({
							title: '提示',
							content: '上传失败',
							showCancel: false,
							confirmText: '确定',
							success: res => {
								if (res.confirm) {
									uni.reLaunch({
										url: '/pages/basic/home/home'
									});
								}
							}
						});
					}
				});
		},
		//来自隐患上报
		
		HYdangerCommit(postData) {
			var self = this;
			var hdDetailData = [];
			let ddd = JSON.stringify(postData);
			uni.request({
				url: self.$store.state.urlPrefix + '/school/ScanQRCode_report',
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					RYXH: self.$store.state.userInfo.RYXH,
					hdDetail: ddd,
					lat: self.lat,
					lon: self.lon
				},
			}).then(data => {
				var [error, res] = data;
				uni.hideLoading()
				if (res.data.code == 0) {
					uni.showModal({
						title: '提示',
						content: '提交成功！',
						showCancel:false,
						confirmText: '确定',
						success: res => {
							if (res.confirm) {
								uni.reLaunch({
									url: '/pages/basic/home/home'
								})
							}
						}
					})
				} else if (res.data.code == 1){
					uni.showModal({
						title: '提示',
						content: res.data.msg,
						showCancel:false,
						confirmText: '确定',
						success: res => {
							if (res.confirm) {
								uni.reLaunch({
									url: '/pages/basic/home/home'
								})
							}
						}
					})
				} else {
					uni.showModal({
						title: '提示',
						content: '上传失败',
						showCancel:false,
						confirmText: '确定',
						success: res => {
							if (res.confirm) {
								uni.reLaunch({
									url: '/pages/basic/home/home'
								})
							}
						}
					})
				}
			})
		},
		
		
		HYsafeCommit() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/school/ScanQRCode_report_safe',
				data: {
					
					RYXH: self.$store.state.userInfo.RYXH,
					HDIDs: self.HYid,
					position: self.position,
					lat: self.lat,
					lon: self.lon
				},
			}).then(data => {
				var [error, res] = data;
				uni.hideLoading()
				if (res.data.code == 0) {
					uni.showModal({
						title: '提示',
						content: '提交成功！',
						showCancel:false,
						confirmText: '确定',
						success: res => {
							if (res.confirm) {
								uni.reLaunch({
									url: '/pages/basic/home/home'
								})
							}
						}
					})
				} else if (res.data.code == 1){
					uni.showModal({
						title: '提示',
						content: res.data.msg,
						showCancel:false,
						confirmText: '确定',
						success: res => {
							uni.reLaunch({
								url: '/pages/basic/home/home'
							})
						}
					})
				} else {
					uni.showModal({
						title: '提示',
						content: '上传失败',
						showCancel:false,
						confirmText: '确定',
						success: res => {
							uni.reLaunch({
								url: '/pages/basic/home/home'
							})
						}
					})
				}
			})
		},
	},
	mounted() {
		this.qrCode = this._props.msg;
		this.qrCodeType = this._props.type;
		this.laizi = this._props.fromm;
		// this.getData();
		this.$emit('hideLoading', 0.1);
		console.log(this.qrCode, this.qrCodeType);
		this.getData();
		this.getLocation();
	}
};
